.n_wrapper{
    height:10vh;
    display: flex;
    justify-content:space-between;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    position: relative;
}
.n_left{
    flex:1;
    align-items: center;
    display: flex;
    gap:2rem
}
.n_name{
  font-size: 1.3rem;
  font-weight: bold;
}
.n_right{
    display: flex;
    flex:1;
    align-items: center;
    justify-content: center;
    font-weight: 400;
}
.n_list{
    flex:10
}
.n_list>ul{
    display: flex;
    gap:2rem ;
    margin:4rem;
}

.text{
   text-decoration: none;
   text-decoration-color: none;
  
}
.text:hover{
    cursor: pointer;
    color:rgb(249, 200, 109)
}