.App {
  margin-left: 40px;
  margin-right: 10px;
}
/* body{
 
} */
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.button{
  border-radius: 34px;
  border:none;
  color: white;
  font-size: 16px;
  padding: 10px;
  background:linear-gradient(180deg,#fdc50f 26.7%, #fd982f 99.36%) ;
  box-shadow: 0px 20px 24px 3px rgba(251, 161, 40 , 0 ,0.42 );
}
.button:hover{
  background: white;
  cursor: pointer;
  color:rgb(249, 200, 109);
  border: 1px solid orange;
}