.intro{
    display:flex;
    height: 77vh;
    margin-top: 6rem;
}
.i_left{
    display:flex;
    position: relative;
    flex:1;
    flex-direction: column;
    gap:2rem
}
.i_name{
  display: flex;
  flex-direction: column;
}
.i_name>:nth-child(1){
    color:rgb(125, 119, 119);
    font-weight: bold;
    font-size: 3rem;
}
.i_name>:nth-child(2){
    color:rgb(217, 177, 101);
    font-weight: bold;
    font-size: 3rem;
}
.i_name>:nth-child(3){
    font-weight: 100;
    font-size: 45px;
    color:gray;
    margin-top: 10px;
}
.i_right{
    flex:1;
    position: relative;
  
}
.i_right>*{
position: absolute;
z-index: 1;
}
.i_rigth>:nth-child(1){
    transform: scale(0.69);
    left: -15%;
    top:-19%;
    }
    .i_rigth>:nth-child(2){
    top:-4.6rem;
    transform: scale(0.67);
    left:-3rem;
    }
    .i_rigth>:nth-child(3){
      transform: scale(1.4);
       left:28%;
        }
.i_icons{
    margin-top:3rem;
    display: flex;
    gap:0rem
}
.i_icons>*{
    transform: scale(0.5);
    cursor: pointer;
}
.i_button{
    width: 6rem;
    height: 2rem;
}