.toggle{
    display: flex;
    justify-content: space-between;
    border: 3px solid orange;
    border-radius: 1rem;
    padding:2px ;
    cursor: pointer;
}
.toggle>*{
   width:1rem;
   height: 1rem;
   color: orange;
}
.t_button{
    border-radius: 100%;
    background-color: orange;
    position: absolute;
}